<template>
  <my-header></my-header>
  <div>
    
  <h1 class="pagetitle">Animal Shelter</h1>
  <ul class="Animal-tiles">
  <li class="Animal-show" v-for="img in imgDocs" :key="img.createdAt" >
     <br>
     <h3>{{img.imgTitle}}</h3>
     <img width="180" height="180" :src="img.imageUrl" alt="img.imgTitle">
     <h5>
      Age: {{img.imgAge}} y.o.
      <br>
      Breed: {{img.imgBreed}}</h5>
  </li>
  </ul>
  </div>
  <my-footer></my-footer>
</template>

<script>
import Header from './Header'
import Footer from './Footer'
import getCollection from './composables/getCollection.js'

  export default {
     components:{
         'my-header': Header,
         'my-footer': Footer
     },

     setup(){

       //Read content
      const {documents } = getCollection('content')

      const { documents: imgDocs } = getCollection('images') // Renaming documents to imgDocs


      return {imgDocs}
     }
  }
</script>

<style lang="scss">
@import "assets/styles/main.scss";

.pagetitle{
    position: relative;
    top: 40px;
    text-align: center;
    font-family: Verdana;
    color: $myblack;
    
}

</style>