<template>
<my-header></my-header>
  <div>
    <br>
    <br>
    <h1>Create: </h1>
    
    <form @submit.prevent="handleSubmit">
     <input type="text" required placeholder="Animal species..." v-model="imgTitle"><br>
     <input type="text" required placeholder="Age..." v-model="imgAge"><br>
     <input type="text" required placeholder="Breed..." v-model="imgBreed"><br>
     <input type="file" class="img-upload" @change="handleChange"> <br><br>
     <button class="btn-create">Create</button>
    </form>
    
    <br>
    <h1>All animals: </h1>
    <div v-for="img in imgDocs" :key="img.createdAt" class="animal-card">
     <h3>{{img.imgTitle}}</h3>
     <p>Age: {{img.imgAge}}
       <br>
        Breed: {{img.imgBreed}}
     </p>
     <img width="160" height="180" :src="img.imageUrl" alt="img.imgTitle">
     <br>
     <button @click="deleteImg(img.filePath, img.id)" class="btn-line">Delete</button>
     <button @click="editContent(doc.id, doc.content)" class="btn-line">Edit</button>
  </div>

  </div>
</template>

<script>
import {ref} from 'vue'
import useCollection from './composables/useCollection.js'
import getCollection from './composables/getCollection.js'
import useDocument from './composables/useDocument.js'
import { timestamp } from './firebase/config'
import useStorage from '@/composables/useStorage'
import Header from './Header'
import Footer from './Footer'

export default {

components:{
         'my-header': Header,
         'my-footer': Footer
     },

setup(){


//Add content
const content = ref("")
const {error, addDoc} = useCollection('content');

    const addContent = async () => {
      const contentObj = {
        content: content.value,
        createdAt: timestamp()
      }
      await addDoc(contentObj)
      if (!error.value) {
        content.value = ''
      }
    }

//Read content
const {documents } = getCollection('content')

//Update Content

const editContent =  async (id, content) =>{
const {updateDoc } = useDocument('content', id)
console.log(content)
await updateDoc({
  content
})

}
//Update Delete
const deleteContent = async (id) => {
const {deleteDoc } = useDocument('content', id)
 
  await deleteDoc();

}

// Image upload

    const file = ref(null)
    const fileError = ref(null)
    const imgTitle = ref('')
    const imgAge = ref('')
    const imgBreed = ref('')


    // allowed file types
    const types = ['image/png', 'image/jpeg']

    const handleChange = (e) => {
      let selected = e.target.files[0]
      console.log(selected)

      if (selected && types.includes(selected.type)) {
        file.value = selected
        fileError.value = null
      } else {
        file.value = null
        fileError.value = 'Please select an image file (png or jpeg)'
      }
    }

       const handleSubmit = async () => {
            const { addDoc } = useCollection('images');
            const { filePath, url, uploadImage } = useStorage()

         if (file.value) {
            await uploadImage(file.value)
            await addDoc({
            imgTitle: imgTitle.value,
            imgAge: imgAge.value,
            imgBreed: imgBreed.value,
            imageUrl: url.value,
            filePath: filePath.value, // so we can delete it later
            createdAt: timestamp()
          })
      }
    }

    const deleteImg = async (imgFilePath, id) => {

       const {deleteImage } = useStorage()

       await deleteImage(imgFilePath)

       const {deleteDoc } = useDocument('images', id)
 
      await deleteDoc();

    }

// delete Image 

const { documents: imgDocs } = getCollection('images') // Renaming documents to imgDocs


return {content, addContent, documents, deleteContent, editContent, handleChange, handleSubmit, imgTitle, imgAge, imgBreed, imgDocs, deleteImg}

}

}

</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  text-align: center;
}

.img-upload{
  width: 180px;
}

.animal-card{
  width: 180px;
  border: solid rgb(75, 74, 74) 1px;
  display: inline-table;
}

.btn-line{
  margin: 5px;
}

.btn-create{
  width: 80px;
  height: 30px;
  background-color: rgb(107, 104, 104);
  color:rgb(131, 207, 154);
  font-family: Arial;
  font-size: large;
}

.btn-create:hover{
  background-color: lightslategray;
}

</style>
